.photo {
  position: relative;
  margin-top: 80px;
  width: $width-photo;
  height: $hight-photo;
  &__title {
    position: absolute;
    font-weight: 800;
    font-size: 12.6px;
    line-height: 150%;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #56CCF2;
    bottom: 144px;
    left: 98px;
  }
  &__text {
    position: absolute;
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 140%;
    color: $main-color;
    bottom: 80px;
    left: 98px;
  }
  &__button {
    position: absolute;
    width: 162.46px;
    height: 64px;
    background-color: #FCC304;
    border-radius: 50px;
    border: none;
    top: calc(100% - 350px);
    left: -80px;
    &::before {
      content: "<";
      font-size: 36px;
      color: $main-color;
      display: block;
      position: absolute;
      top: 9px;
      left: 30px;
    }
    &::after {
      content: ">";
      font-size: 36px;
      color: $main-color;
      display: block;
      position: absolute;
      top: 9px;
      right: 30px;
    }
  }
  &__networks {
    position: absolute;
    left: calc( 1146px + 65px );
  }
}

.networks {
  display: flex;
  transform: rotate(90deg);
  &__social{
    font-weight: 800;
    font-size: 13px;
    line-height: 150%;
    color: #253757;
    text-transform: uppercase;
    padding-left: 85px;


  }
}

