.header {
  position: relative;
  &__top{
  position: absolute;
  background-color: #56CCF2;
  position: relative;
  width: 1029px;
  height: 848px;
  }

  &__rotate {
    position: absolute;
    width: 1108px;
    height: 601px;
    left: 583px;
    background: #56CCF2;
    transform: rotate(-65deg);
  }

  &__nav {
    position: absolute;
    top: 51px;
    left: 227px;

  }
}
 .nav {
   color: $main-color;
  &__list {
    display: flex;


  }
  &__item {
    padding: 0 32px;

    &_title {
      font-weight: 700;
      font-size: 32px;
      line-height: 100%;
      color: $main-color;
      padding-right: 80px;
    }
    &_act {
      padding: 0 70px;
    }
  }

  &__link {
    color: $main-color;
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
    &_active {
     position: relative;
     &::after {
      position: absolute;
      content: "";
      width: 64px;
      height: 4px;
      left: 0;
      right: 0;
      bottom: -10px;
      background: #FCC304;
      border-radius: 32px;
      }
    }
  }
}

@media screen and (max-width: $maxwidth) {
  .nav__list {
    flex-direction: column;
    align-items: center;
  }
  .nav__item_title {
    padding: 0;
  }
}
